.recorder-container {
  background: rgba(255, 255, 255, 0.2);           /* semi-transparent white */
  -webkit-backdrop-filter: blur(20px);            /* Safari */
  backdrop-filter: blur(20px);                    /* Chrome, FF, Edge */
  border: 1px solid rgba(255, 255, 255, 0.3);
  max-width: 600px;
  margin: 2rem auto;
  border-radius: 16px;
  box-shadow: 0 5px 20px rgba(0, 0, 0, 0.08);
  overflow: hidden;
  font-family: 'Segoe UI', sans-serif;
  position: relative;
  min-height: 90vh;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.title {
  background: rgba(245, 245, 245, 0.2);           /* semi-transparent white */
  -webkit-backdrop-filter: blur(20px);            /* Safari */
  backdrop-filter: blur(20px);                    /* Chrome, FF, Edge */
  border: 1px solid rgba(255, 255, 255, 0.3);
  display: flex;
  justify-content: space-around;
  height: fit-content;
  align-items: center;
  border-bottom: none;
}

.chat-title {
  text-align: center;
  font-size: 1.3rem;
  padding: 1rem;
  color: #eee;
}

.chat-window {
  padding: 1rem;
  max-height: 400px;
  overflow-y: auto;
}

.message {
  display: flex;
  margin-bottom: 1rem;
}

.message.user {
  justify-content: flex-end;
}

.message.bot {
  justify-content: flex-start;
}

.bubble {
  background: #59a6b4;
  padding: 0.75rem 1rem;
  border-radius: 1rem;
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.message.user .bubble {
  background: #89b459;
}

.status-banner {
  background: rgba(249, 249, 249, 0.2);           /* semi-transparent white */
  -webkit-backdrop-filter: blur(20px);            /* Safari */
  backdrop-filter: blur(20px);                    /* Chrome, FF, Edge */
  border: 1px solid rgba(255, 255, 255, 0.3);
  width: 100%;
  padding-block: .5rem;
  text-align: center;
  font-weight: 500;
  color: #eee;
  display: flex;
  justify-content: space-around;
  align-items: center;
  gap: 0.5rem;
  border-top: none;
}

.record-btn {
  background:  #89b459;
  border: none;
  color: #eee;
  border-radius: 50%;
  width: 50px;
  height: 50px;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: background 0.3s ease;
  box-shadow: 0px 0px 10px gray;
}

.record-btn:hover:enabled {
  background:  #7da54f;
}

.record-btn:disabled {
  background: #aaa;
  cursor: not-allowed;
}

.spinner {
  width: 16px;
  height: 16px;
  border: 3px solid #ccc;
  border-top: 3px solid #4caf50;
  border-radius: 50%;
  animation: spin 0.8s linear infinite;
}

.pulse {
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background: #4caf50;
  animation: pulse 1.2s infinite;
}

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}

@keyframes pulse {
  0%, 100% {
    transform: scale(0.8);
    opacity: 0.5;
  }
  50% {
    transform: scale(1.3);
    opacity: 1;
  }
}

.recording-icon{
  display: flex;
  gap: 5px;
  justify-content: center;
  align-items: center;
}

.disconnect-btn {
  color: #ce3939;
  border: none;
  background: transparent;
  cursor: pointer;
}


.reconnect-btn {
  color:   #39b0ce;
  border: none;
  background: transparent;
  cursor: pointer;
}

.download-btn {
  color: #eee;
  width: fit-content;
  min-width: 39px;
  cursor: pointer;
  border:none;
  background: transparent;
}